import {
	getSwiper,
	request
} from '../request.js'



import Loading from '../common/loading/loading.vue'
export default {
	data() {
		return {
			baseURL:this.$api,
			locationName: '获取当前位置',
			latitude: '',
			longitude: '',
			list: [],
			loading: false,
			page: 1,
			loadMore: true,
			$src: this.$src
		};
	},
	components: {
		Loading
	},
	onLoad() {
		let that = this;
		// 定位
		uni.getStorage({
			key: "position",
			fail: function(res) { 
				that.loading = true;
				that.getLocartion();
				
			},
			success: function(res) {
				that.latitude = res.data[0];
				that.longitude = res.data[1];
				that.getGoodsList();
				uni.getStorage({
					key: "positionName",
					fail: function(res) {
						that.getLocationName();
					},
					success: function(res) {
						that.locationName = res.data;
					}
				})
			}
		})
	},
	// 加载更多商家
	onReachBottom() {
		if(this.loadMore) {
			this.page += 1;
			this.getGoodsList();
		}
	},
	methods: {
		getlocationpoint() {
			let that = this;
			let latitude;
			let longitude;
			if (wechat.isWechat()) {
				wechat.location(function(res) {
					latitude = res.latitude;
					longitude = res.longitude;
					that.latitude = latitude;
					that.longitude = longitude;
					uni.setStorage({
						key: 'position',
						data: Array(latitude, longitude)
					})
					that.getLocationName();
					that.getGoodsList();
				});
			} else {
				if (navigator.geolocation) {
					navigator.geolocation.getCurrentPosition(
						function(position) {
							latitude = position.coords.latitude; //获取纬度
							longitude = position.coords.longitude; //获取经度
							that.latitude = latitude;
							that.longitude = longitude;
							uni.setStorage({
								key: 'position',
								data: Array(latitude, longitude)
							})
							that.getLocationName();
							that.getGoodsList();
						});
				} else {
					uni.showToast({
						title: '不支持定位功能',
						icon: 'none'
					});
				}
			}
		},
		getLocartion() {
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(function() {
				uni.hideLoading();
			}, 2000);
			let that = this;
			let position = that.getlocationpoint();
			uni.hideLoading();
		},
		// 获取城市名
		getLocationName() {
			let that = this;
			request({
					url: 'index.php?s=/wap/index/apiGetLocateName',
					data: {
						latitude: that.latitude,
						longitude: that.longitude,
					}
				})
				.then(res => {
					that.locationName = res.data.data.result.ad_info.city;
					uni.setStorage({
						key: 'positionName',
						data: res.data.data.result.ad_info.city
					})
				});
		},
		// 获取商品
		getGoodsList() {
			let that = this;
			uni.showLoading({
				title:"加载中",
				mask:true
			});
			request({
					url: 'index.php?s=/wap/index/apiGetBoomGoods',
					data: {
						latitude: that.latitude,
						longitude: that.longitude,
						page: that.page
					}
				})
				.then(res => {
					uni.hideLoading();
					that.flag = true;
					this.loading = true
					if(res.data.list.length == 0) {
						that.loadMore = false;
						uni.showToast({
							title: '暂无更多数据',
							icon: 'none'
						})
					}
					else {
						that.loading = true;
						that.list = [...that.list,...res.data.list];
					}
				});
		},
		downLineDetail(id) {
			uni.navigateTo({
				url: '/pages/common/goods-detail/goods-detail?id='+id
			})
		}
	}
}
